.project-content__toolbar{
    padding: 0 3rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: calc(var(--header-height) - 1rem);
    width: 100%;
    border-bottom: 1px solid var(--border-color-primary);
    position: sticky;
    top: var(--header-height) ;
    background: white;
    z-index: 2;

    & .btns-div{
        display: flex;
        gap: .5rem;
    }

    & .avatars{
        margin-left: auto;
        display: flex;
        gap: .2rem;
        margin-right: 1.4rem;
        & img{
            width: 2.6rem;
            height: 2.6rem;
        }
    }
}