.task-card{
    --padding-sides:1.5rem;
    width: 100%;
    padding: 1rem var(--padding-sides) 0;
    border-radius: var(--br-md);
    border: 1px solid var(--border-color-primary);
    display: flex;
    flex-direction: column;
    gap: 1.2rem;
    position: relative;
    z-index: 1;
    background-color: white;
    transform-style: preserve-3d;

    & .extra-bg{
        position: absolute;
        width: 98%;
        height: 5rem;
        top: calc(100% - 4.4rem);
        left: 50%;
        border-radius: var(--br-md);
        border: 1px solid var(--border-color-primary);
        transform: translateX(-50%) translateZ(-10px);
    }

    & .flyte{
        text-transform: uppercase;
        font-size: 1rem;
        color: #7F8995;
        letter-spacing: .6px;
    }
    & .title{
        font-size: 1.6rem;
        font-weight: 700;
    }

    & .description{
        font-size: 1.2rem;
        color: #7F8995;
        line-height: 1.4;
        font-weight: 400;
    }

    & .tag-and-avatars{
        display: flex;
        align-items: center;
        justify-content: space-between;
        & .tag{
        padding: .6rem;
        color: #2F6BFE;
        background: #EAF0FF;
        width: fit-content;
        font-weight: 700;
        font-size: 1.2rem;
        border-radius: 4px;
        }
        & .avatars img{
            width: 3rem;
            height: 3rem;
        }
        & .avatars:not(:first-child) img{
            margin-left: -1rem;
            border: 2px solid white;
            border-radius: 50%;
        }
    }
    & .bottom-bar{
        display: flex;
        gap: .5rem;
        padding: 1.5rem 0;
        // border-top: 1px solid red;
        position: relative;

        &::after{
            content: "";
            position: absolute;
            top: 0;
            left: 50%;
            transform: translateX(-50%);
            width: calc(100% + var(--padding-sides) *2);
            height: 1px;
            background-color: var(--border-color-primary);
        }
        & > span{
            display: flex;
            align-items: center;
            gap: .5rem;
            font-weight: 500;
            font-size: 1.2rem;
            color: #98A5B3;
            &:nth-child(3){
                margin-left: auto;
            }
        }
    }
}