.project-content__main{
    display: grid;
    grid-template-columns: repeat(3,1fr);
    gap: 4.5rem;
    // height: 100%;
    padding: 0 3rem;
    & > div{
        width: 100%;
        // height: 80rem;
        display: flex;
        padding: 2rem 0;
        flex-direction: column;
        // gap: 3rem;

        &:nth-child(2){
            & .header img{
                width: 1.9rem;
                height: 1.9rem;
            };
        }

        & .header{
            display: flex;
            align-items: center;
            justify-content: space-between;
            position: relative;
            padding-bottom: 1.5rem;

            &::after{
                content: "";
                position: absolute;
                bottom: 0;
                left: 0;
                height: 2px;
                width: 100%;
                border-radius: 7px;
            }

            & .header-left{
                display: flex;
                align-items: center;
                gap: .8rem;
                font-size: 1.4rem;
                & h5:nth-child(2){
                    font-weight: 700;
                }
                & span:nth-child(3){
                    font-weight: 700;
                    font-size: 1.2rem;
                    border: 1px solid #EAEDF0;
                    background: #F7F9FA;
                    padding: .1rem .8rem;
                    border-radius: 6px;
                }
            }
            & .header-right{
                display: flex;
                align-items: center;
                gap: .6rem;
                font-size: 1.8rem;
                & svg{
                    fill: #A9B4C0;
                }
            }
        }
    }
}