.project-details {
    height: 100%;
    width: 100%;
    border-right: 1px solid var(--border-color-primary);

    & .project-name {
        height: var(--header-height);
        display: flex;
        align-items: center;
        padding: 0 1rem;
        font-size: 1rem;
    }

    & .project-image {
        & img {
            width: 100%;
        }
    }

    & .project-progress {
        // height: 5rem;
        border-bottom: 1px solid var(--border-color-primary);
        padding: 1.2rem 1.6rem 1.5rem;

        &__stats {
            display: flex;
            align-items: center;
            justify-content: space-between;
            margin-bottom: .8rem;

            &-level {
                font-weight: 700;
                font-size: 1.2rem;
                display: flex;
                align-items: center;
                & svg{
                    font-size: 1.4rem;
                    margin-right: .2rem;
                }
            }

            &-percentage {
                font-weight: 400;
                font-size: 1.2rem;
                color: var(--text-grey-primary);
            }
        }

        &__progress-bar {
            width: 100%;
            height: .8rem;
            position: relative;
            border-radius: var(--br-lg);
            background-color: #F2F5F7;
            &::after{
                border-radius: var(--br-lg);
                content: "";
                position: absolute;
                top: 0;
                left: 0;
                width: var(--project-progress);
                height: 100%;
                background: repeating-linear-gradient( 115deg, #4734FE, #4734FE 10px, #8478FE 10px, #8478FE 20px);
            }

            &__icon{
                position: absolute;
                top: 50%;
                left: calc(var(--project-progress) - 5%);
                transform: translateY(-50%);
                background: white;
                border: 2px solid #EAEDF0;
                border-radius: 100%;
                display: flex;
                align-items: center;
                justify-content: center;
                width: 1.6rem;
                height: 1.6rem;
                z-index: 2;
                
            }
        }
    }

    & .project-task{
        padding: 2.5rem 1.6rem ;

        &-list{
            list-style: none;
            display: flex;
            flex-direction: column;
            gap: 1.7rem;
            font-weight: 400;
            & li{
                font-size: 1.2rem;
                color: var(--text-grey-secondary);
                display: flex;
                align-items: center;
                justify-content: space-between;
                & svg{
                    font-size: 1rem;
                }
            }
        }
    }
}